<script>
export default {
  data() {
    return {
      // langauge tracking object : reusable component
      langs: ["日本語", "English", "中文繁体字", "中文简体字"],
      language: this.$t("language"),

      // For screenshots swiper
      swiperOption: {
        slidesPerView: 2,
        spaceBetween: 50,

        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        breakpoints: {
          1200: {
            slidesPerView: 3,
            spaceBetween: 200
          },
          992: {
            slidesPerView: 2,
            spaceBetween: 200
          },
          768: {
            slidesPerView: 1,
            spaceBetween: 200
          },
          576: {
            slidesPerView: 1,
            spaceBetween: 100
          }
        }
      }
    };
  },
  methods: {
    languageSwitcher(lang) {
      // localstorage to remember users langauge setting
      localStorage.setItem("lang", lang);
      // i18n.locale - define refered selected language
      this.$i18n.locale = lang;

      // langauge tracking for reference at dropdown span
      this.language = lang;

      // language switching
      if (lang == "日本語") {
        var post_lang_jp = "jp";
        localStorage.setItem("post_lang", post_lang_jp);
      } else if (lang == "English") {
        var post_lang_en = "en";
        localStorage.setItem("post_lang", post_lang_en);
      } else if (lang == "中文繁体字") {
        var post_lang_cnT = "cnTraditional";
        localStorage.setItem("post_lang", post_lang_cnT);
      } else if (lang == "中文简体字") {
        var post_lang_cn = "cnSimplify";
        localStorage.setItem("post_lang", post_lang_cn);
      }
    }
  },
  created: function() {
    if (localStorage.getItem("lang") != null) {
      this.$i18n.locale = localStorage.getItem("lang");
      this.language = localStorage.getItem("lang");
    }
  }
};
</script>

