import Vue from "vue";
import App from "./App.vue";
//import Router plugin
import Router from "vue-router";

// import related dependencies for usage
// import bootstrap vue -> and the must needed .css to allow communication between bootstrap - bootstrap vue
import BootstrapVue from "bootstrap-vue";
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";

import AOS from "aos"; // Animate on scroll
import "aos/dist/aos.css";

import BackToTop from "vue-backtotop"; //Back to top plugin

import VueI18n from "vue-i18n"; //internationalization

import VueAwesomeSwiper from "vue-awesome-swiper"; //photo or pannel swiper
import "swiper/dist/css/swiper.css";

// implement or initiatize dependancies or plugin for Vue; Vue.use()
Vue.use(Router);
Vue.use(BootstrapVue);

Vue.use(BackToTop);
Vue.use(VueI18n);

Vue.use(VueAwesomeSwiper); //photo or pannel swiper

// ^^ END OF -> implement or initiatize dependancies or plugin for Vue;

// import of views & components for APP
import testIndex from "./views/testIndex.vue";

Vue.config.productionTip = false;

// defining routes : ~> into Router; for mapping routes
const router = new Router({
  routes: [
    { path: "/", component: testIndex },
    { path: "/Home", component: testIndex, template: "id=#Home" },
    { path: "/About", component: testIndex, template: "id=#About" },
    { path: "/Services", component: testIndex, template: "id=#Services" },
    { path: "/Media", component: testIndex, template: "id=#Media" },
    { path: "/Download", component: testIndex, template: "id=#Download" }
  ]
});

// Langauge json retrival location - internalization
// !!!NOTE: i18n only accept variable with the exact name " messages " !!!
const messages = {
  日本語: require("./components/locales/jp.json"),
  English: require("./components/locales/en.json"),
  中文繁体字: require("./components/locales/cn(traditional).json"),
  中文简体字: require("./components/locales/cn(simplify).json")
};

// set the initial langauge and assign language file referal location - internalization
const i18n = new VueI18n({
  messages,
  locale: "English"
});

new Vue({
  created() {
    AOS.init();
  },
  i18n,
  router,
  render: h => h(App)
}).$mount("#app");
