<template>
  <div id="app">
    <loading
      :active.sync="isLoading"
      :can-cancel="true"
      :on-cancel="onCancel"
      :is-full-page="fullPage"
    ></loading>
    
    <router-view/>
  </div>
</template>

<script>
// import loading dependancy or plugin
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  provide() {
    return {
      loading: this.reload,
      reload: this.reload
    };
  },

  components: {
    Loading
  },

  // define method local object(variable) to hold local tracking value
  data() {
    return {
      isRouterAlive: true,
      isloading: false
    };
  },

  methods: {
    //define methods for the top script 'return'
    reload() {
      isRouterAlive: false;
      this.$nextTick(function() {
        this.isRouterAlive = true;
      });
    },

    // method to switch loading state.
    loading() {
      this.isloading = !this.isloading;
    }
  }
};
</script>

